import React from "react"
import { Link } from "gatsby"
import TwoColumnSection from "../twoColumnSection/TwoColumnSection"
import styles from "./imageAndTextDetail.module.sass"
import arrow from "../../images/icon-arrow-next.svg"

class ImageAndTextDetail extends React.Component {
  render() {
    const {
      title,
      description,
      logos,
      linkText,
      linkUrl,
      children,
      isImageOnLeft,
      logoImagesWidth,
      noPadding,
      rowGap,
      columnGap,
      isComingSoon,
    } = this.props
    return (
      <TwoColumnSection
        noPadding={noPadding}
        rowGap={rowGap}
        isImageOnLeft={isImageOnLeft}
        columnGap={columnGap ? columnGap : "80px"}
      >
        {isImageOnLeft && (
          <div className={styles.imageContainer}>{children}</div>
        )}
        <div className={styles.text}>
          <h3>
            {title}{" "}
            {isComingSoon && (
              <span className={styles.comingSoon}>Coming Soon</span>
            )}
          </h3>
          <h5>{description}</h5>
          {logos && (
            <>
              <h6>KintoHub abstracts:</h6>
              <div
                className={styles.logoContainer}
                style={{ maxWidth: logoImagesWidth }}
              >
                <img
                  src={logos}
                  className={styles.logos}
                  alt="companies replaced by KintoHub"
                />
              </div>
            </>
          )}
          {linkUrl && (
            <Link to={linkUrl}>
              {linkText} <img src={arrow} alt="" />
            </Link>
          )}
        </div>
        {!isImageOnLeft && (
          <div className={styles.imageContainer}>{children}</div>
        )}
      </TwoColumnSection>
    )
  }
}

export default ImageAndTextDetail
