import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "../components/layout"

import DeployHero from "../components/features/deploy/DeployHero"
import DeploymentStrategies from "../components/features/deploy/DeploymentStrategies"
import SEO from "../components/seo"
import TwoColumnSection from "../components/twoColumnSection/TwoColumnSection"
import ImageAndTextDetail from "../components/imageWithTextDetail/ImageAndTextDetail"
import ContactFormContainer from "../containers/ContactFormContainer"
import styles from "../pageStyles/features.module.sass"

class DeployPage extends React.Component {
  render() {
    const { data } = this.props
    return (
      <Layout>
        <SEO
          title="Deploy"
          keywords={[`kintohub`, `microservices`, `serverside`, `features`]}
        />
        <DeployHero />
        <div className={styles.howDeployWorks}>
          <h2>How Deploy Works</h2>

          <div className={styles.Row}>
            <TwoColumnSection columnGap="80px">
              <div className={styles.howDeployWorksText}>
                <h3>Combine Any Number of KintoBlocks</h3>
                <h5>
                  Manage and configure your fleet of potentially hundreds of
                  services in a single click. Override configuration to be
                  different in <strong>dev</strong> and <strong>prod</strong> like API Keys, beta
                  features and more.
                </h5>
              </div>

              <Img
                fluid={data.deployCombine.childImageSharp.fluid}
                className="rounded-corner-8"
              />
            </TwoColumnSection>
          </div>

          <div className={styles.Row}>
            <TwoColumnSection columnGap="80px" isImageOnLeft={true}>
              <Img
                fluid={data.deployDeploy.childImageSharp.fluid}
                className="rounded-corner-8"
              />
              <div className={styles.howDeployWorksText}>
                <h3>Deploy</h3>
                <h5>
                  Safely deploy your services into an environment with the
                  strategy of your choice. <b>Rolling</b>, <b>Canary</b>,{" "}
                  <b>A/B</b> testable and <b>shadow</b> deployments{" "}
                  <a href="https://github.com/kintohub/kintohub-docs/issues">
                    (coming soon)
                  </a>{" "}
                  are all possible with KintoHub.
                </h5>
              </div>
            </TwoColumnSection>
          </div>

          <div className={styles.Row}>
            <TwoColumnSection columnGap="80px">
              <div className={styles.howDeployWorksText}>
                <h3>Configure Any Environment</h3>
                <h5>
                  Create one or many environments to create your development
                  workflow. Each environment can set or override any setting
                  such as <b>hostnames</b> and <b>SSL encryption</b> (coming
                  soon),{" "}
                  <b>environment variables</b> or <b>high availability</b>.
                </h5>
              </div>
              <Img
                fluid={data.deployConfigure.childImageSharp.fluid}
                className="rounded-corner-8"
              />
            </TwoColumnSection>
          </div>
        </div>

        <div className={styles.deploymentStrategies}>
          <div className={styles.deploymentStrategiesInner}>
            <h2>Deployment Strategies</h2>
            <DeploymentStrategies />
          </div>
        </div>
        <div className={styles.rollbackToPrevious}>
          <ImageAndTextDetail
            isImageOnLeft={true}
            title="Rollback to Previous Version"
            description="When an issue arises, you can simply rollback to any previous version with one click. Restore your environment to its original state in seconds."
          >
            <Img
              fluid={data.deployRollback.childImageSharp.fluid}
              className="rounded-corner-8"
            />
          </ImageAndTextDetail>
        </div>
        <ContactFormContainer />
      </Layout>
    )
  }
}

export default DeployPage

export const pageQuery = graphql`
  query {
    deployCombine: file(relativePath: { eq: "deploy-combine@2x.png" }) {
      ...fluidImage
    }
    deployDeploy: file(relativePath: { eq: "deploy-deploy@2x.png" }) {
      ...fluidImage
    }
    deployConfigure: file(relativePath: { eq: "deploy-config@2x.png" }) {
      ...fluidImage
    }
    deployRollback: file(relativePath: { eq: "deploy-rollback@2x.png" }) {
      ...fluidImage
    }
  }
`
