import React from "react"
import styles from "./twoColumns.module.sass"

class TwoColumnSection extends React.Component {
  render() {
    const {
      children,
      rowHeight,
      columnGap,
      noPadding,
      isImageOnLeft,
      backgroundColor,
      title
    } = this.props
    const paddingStyle = noPadding ? styles.noPadding : ""
    const gridStyles = {
      gridColumnGap: columnGap ? columnGap : "20px",
      gridAutoRows: rowHeight ? rowHeight : "auto",
      backgroundColor: backgroundColor ? backgroundColor : "#FFFFFF",
    }
    const columnStyles = isImageOnLeft ? styles.innerReverse : styles.inner
    return (
      <div className={[styles.twoColumns, paddingStyle].join(" ")}>
        {title && (
          <div className={styles.titleContainer}>
            <h2>{title}</h2>
          </div>
        )}
        <div className={columnStyles} style={gridStyles}>
          {children}
        </div>
      </div>
    )
  }
}

export default TwoColumnSection
